import React from "react";
import breeds from '../../breeds.json';
import API from '../../utils/API';

class Search extends React.Component {
  state = {
    search: "",
    results: []
  }

  handleSearch = event => {
    event.preventDefault()

    API.search(this.state.search)
      .then(results => this.setState({ results: results.data.message }))
      .catch(err => console.log(err))
  }

  handleChange = event => {
    this.setState({
      search: event.target.value
    })
  }
  
  render(){
    return (
      <div>
        <h1>Search Page</h1>
        <form>
          <label for="breeds">Choose a breed:</label>
          <select id="breed" name="breeds" onChange={this.handleChange}>
            {breeds.map(breed => <option value={breed}>{breed}</option>)}
          </select>
          <button className="btn btn-success" onClick={this.handleSearch}>Search</button>
        </form>

        <div className="container">
          {this.state.results.map(dog => <img src={dog} />)}
        </div>
      </div>
    );
  }
}

export default Search;
